import { useState } from 'react';
import { Search } from '@carbon/react';

const search = ({e, dataSource, setSearchTerm, setDataState}) => {

  let results = dataSource;
  
  if (e.target.name === 'search_term') {
    setSearchTerm(e.target.value)
    if (e.target.value) {
      results = results.filter(item => {
        return (
          item.title.toLowerCase().includes(e.target.value.toLowerCase()) 
          || (item.description ? item.description.toLowerCase().includes(e.target.value.toLowerCase()) : false) 
          || (item.heldby ? item.heldby.toLowerCase().includes(e.target.value.toLowerCase()) : false)
        );
      });
    }
  }
  
  setDataState(results);

}

const WikibaseSearch = ({dataSource, setDataState}) => {
    
  const [searchTerm, setSearchTerm] = useState('');

  return (
    <div id='search'>
      <Search size="lg" placeholder='Search' labelText='Search' closeButtonLabelText='Clear search input' name='search_term' value={searchTerm} onChange={(e) => search({e, dataSource, setSearchTerm, setDataState})} onClear={()=>setSearchTerm('')} />
      <p className='help'>Note: This is a simple search example that performs exact string match on dataset title, description and holder name.</p>
    </div>
  )

}

export default WikibaseSearch;