import { memo, useState, useEffect } from 'react';
import WikibaseSearch from './WikibaseSearch';
import { FlexGrid, Row, Column, Tile } from '@carbon/react';

const WikibaseItem = memo(({item}) => (
  <div className='wikibase-item' key={item.uri}>
    <div className='wikibase-item-data'>
      <p className='wikibase-item-title'>{item.title}</p>
      <p className='wikibase-item-description'>{item.hasOwnProperty('description') ? item.description : 'N/A'}</p>
    </div>
    <div className='wikibase-item-metadata'>
      <div className='metadata'>
        <p>Held By</p>
        <p>{item.heldby ? item.heldby : 'N/A'}</p>
      </div>
      <div className='metadata'>
        <p>Datasheet</p>
        <p>{item.datasheet ? (<a href={item.datasheet}>Digital Cultural heritage Datasheet</a>) : ('N/A')}</p>
      </div>
      <div className='metadata'>
        <p>Data Register</p>
        <p><a href={item.uri}>The National Collection Data Register</a></p>
      </div>
    </div>
  </div>
));

const Wikibase = () => {
    
  const [dataSource, setDataSource] = useState([]);
  const [dataState, setDataState] = useState([]);

  useEffect(() => {

    let results = [];

    const fetchData = async() => {
      const endpointUrl = 'https://bradford-collections.wikibase.cloud/query/sparql';
      const sparqlQuery = `PREFIX bcwd: <https://bradford-collections.wikibase.cloud/entity/>
      PREFIX bcwds: <https://bradford-collections.wikibase.cloud/entity/statement/>
      PREFIX bcwdv: <https://bradford-collections.wikibase.cloud/value/>
      PREFIX bcwdt: <https://bradford-collections.wikibase.cloud/prop/direct/>
      PREFIX bcp: <https://bradford-collections.wikibase.cloud/prop/>
      PREFIX bcps: <https://bradford-collections.wikibase.cloud/prop/statement/>
      PREFIX bcpq: <https://bradford-collections.wikibase.cloud/prop/qualifier/>
      PREFIX bcpsv: <https://bradford-collections.wikibase.cloud/prop/statement/value/>
      PREFIX bcpqv: <https://bradford-collections.wikibase.cloud/prop/qualifier/value/>

      SELECT ?dataset ?datasetLabel ?datasetDescription ?datasetHeldBy ?datasheetURL
      WHERE
      {
        ?dataset bcwdt:P13 bcwd:Q1.
        OPTIONAL {?dataset bcwdt:P5 ?datasetHeldBy.}
        OPTIONAL {?dataset bcwdt:P24 ?datasheetURL.}
        SERVICE wikibase:label { bd:serviceParam wikibase:language "[AUTO_LANGUAGE],en-gb,en". } 
      }
      ORDER BY ASC(UCASE(?datasetLabel))`;
      const url = endpointUrl + '?query=' + encodeURIComponent( sparqlQuery );
      const response = await fetch(url, {
        method: 'get',
        headers: new Headers({
          'Accept': 'application/sparql-results+json'
        })
      });
      const json = await response.json();
      
      json.results.bindings.forEach(item => {
        results.push({
          'uri': item.dataset.value,
          'title': item.datasetLabel.value,
          'description': item.hasOwnProperty('datasetDescription') ? item.datasetDescription.value : null,
          'heldby': item.hasOwnProperty('datasetHeldBy') ? item.datasetHeldBy.value : null,
          'datasheet': item.hasOwnProperty('datasheetURL') ? item.datasheetURL.value : null
        })
      });
      
      setDataSource(results);
      setDataState(results);

    }

    fetchData();

  }, []);
  
  return (
    <>
      <WikibaseSearch dataSource={dataSource} setDataState={setDataState} />
      <div id='wikibase'>
        <FlexGrid className='cds_grid'>
          <Row className='cds_row'>
          { dataState.length ? dataState.map(item => (
            <Column sm={12} md={4} lg={4} className="cds_col_hspace" key={item.uri}>
              <div className="cds_col_vspace">
                <Tile className='cds_tile'>
                  <WikibaseItem item={item}/>
                </Tile>
              </div>
            </Column>
          )) : <p>No dataset found.</p> }
          </Row>
        </FlexGrid>
      </div>
    </>
  );

}

export default Wikibase;